/* skills.css */
.skills-section {
  padding: var(--spacing-xl) var(--spacing-md);
  background: transparent;
}

.skills-container {
  max-width: 1200px;
  margin: 0 auto;
}

.skills-header {
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.skills-header h2 {
  color: var(--white);
  font-size: 2.5rem;
  margin-bottom: var(--spacing-sm);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-lg);
}

.skill-category {
  background: rgba(17, 34, 64, 0.7);
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-lg);
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.skill-category::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background: var(--secondary);
  transform: scaleY(0);
  transition: transform 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.skill-category:hover::before {
  transform: scaleY(1);
}

.skill-category:hover {
  transform: translateX(5px);
  box-shadow: 0 20px 30px -15px rgba(2, 12, 27, 0.7);
}

.skill-category h3 {
  color: var(--secondary);
  font-size: 1.4rem;
  margin-bottom: var(--spacing-md);
  display: flex;
  align-items: center;
  gap: 1rem;
}

.skill-category h3::after {
  content: '';
  flex: 1;
  height: 1px;
  background: rgba(100, 255, 218, 0.1);
}

.skills-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: var(--spacing-md);
}

.skill-item {
  position: relative;
  padding-left: var(--spacing-md);
  color: var(--text-secondary);
  transition: var(--transition);
}

.skill-item::before {
  content: "▹";
  position: absolute;
  left: 0;
  color: var(--secondary);
  font-size: 1.2rem;
  transition: var(--transition);
}

.skill-item:hover {
  color: var(--white);
  transform: translateX(10px);
}

.skill-item:hover::before {
  transform: scale(1.2);
}

.skill-detail {
  margin-top: var(--spacing-xs);
}

.skill-progress-bar {
  height: 3px;
  background: rgba(35, 53, 84, 0.7);
  border-radius: var(--border-radius-sm);
  overflow: hidden;
  margin-top: 0.5rem;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--secondary) 0%, rgba(100, 255, 218, 0.5) 100%);
  border-radius: var(--border-radius-sm);
  transition: width 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
}

/* Add some hover animations for the progress bars */
.skill-item:hover .skill-progress {
  background: linear-gradient(90deg, var(--secondary) 0%, rgba(100, 255, 218, 0.8) 100%);
}

@media (max-width: 768px) {
  .skills-section {
    padding: var(--spacing-lg) var(--spacing-sm);
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
  
  .skills-list {
    grid-template-columns: 1fr;
  }
}
