/* app.css */

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px; /* This accounts for the fixed navigation */
}

:root {
  --primary: #0a192f;
  --secondary: #64ffda;
  --text-primary: #ccd6f6;
  --text-secondary: #8892b0;
  --bg-light: #112240;
  --bg-lighter: #233554;
  --white: #e6f1ff;
  --transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  
  /* Add standardized spacing and border values */
  --border-radius-sm: 4px;
  --border-radius-md: 8px;
  --border-radius-lg: 12px;
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 4rem;
  --spacing-xl: 6rem;
  
  /* Hover effect variables */
  --hover-transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  --hover-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
  --hover-y-offset: -5px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #0a192f 0%, #112240 100%);
  color: #ccd6f6;
  min-height: 100vh;
  line-height: 1.6;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

section {
  padding: var(--spacing-xl) var(--spacing-md);
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.6s ease, transform 0.6s ease;
  position: relative;
  z-index: 2;
  background: transparent;
}

section.visible {
  opacity: 1;
  transform: translateY(0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary);
}

::-webkit-scrollbar-thumb {
  background: var(--bg-lighter);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--secondary);
}

/* Add loading animation styles */
.loading-screen {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--primary);
}

.loader {
  width: 50px;
  height: 50px;
  border: 3px solid var(--bg-lighter);
  border-radius: 50%;
  border-top-color: var(--secondary);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Add Back to Top button styles */
.back-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: var(--bg-light);
  color: var(--secondary);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  transition: var(--transition);
  z-index: 999;
}

.back-to-top:hover {
  background: var(--bg-lighter);
  transform: translateY(-5px);
}

/* Remove the texture overlay */
body::before {
  display: none;
}

/* Ensure all text is readable */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  line-height: 1.2;
}

h1 { font-size: 2.5rem; }
h2 { font-size: 2rem; }
h3 { font-size: 1.5rem; }

/* Adjust spacing for mobile */
@media (max-width: 768px) {
  :root {
    --spacing-xl: 4rem;
    --spacing-lg: 3rem;
    --spacing-md: 1.5rem;
    
    /* Reduce hover effects on touch devices */
    --hover-y-offset: -2px;
  }

  h1 { font-size: 2rem; }
  h2 { font-size: 1.75rem; }
  h3 { font-size: 1.25rem; }
}

p {
  color: #8892b0;
  font-size: 1.1rem;
  line-height: 1.8;
}

/* Add scroll animations */
@keyframes fadeUpIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fadeUpIn {
  animation: fadeUpIn 0.6s ease forwards;
}

/* Common hover effects */
.hover-lift {
  transition: var(--hover-transition);
}

.hover-lift:hover {
  transform: translateY(var(--hover-y-offset));
}

.hover-shadow {
  transition: var(--hover-transition), box-shadow 0.3s ease;
}

.hover-shadow:hover {
  box-shadow: var(--hover-shadow);
}

/* Improve mobile responsiveness */
@media (max-width: 768px) {
  :root {
    /* Adjust spacing for mobile */
    --spacing-xl: 3rem;
    --spacing-lg: 2rem;
    --spacing-md: 1rem;
    
    /* Reduce hover effects on touch devices */
    --hover-y-offset: -2px;
  }

  .container {
    padding: 0 1rem;
  }

  h1 { font-size: 1.8rem; }
  h2 { font-size: 1.5rem; }
  h3 { font-size: 1.2rem; }
  
  p {
    font-size: 1rem;
    line-height: 1.6;
  }
}

/* Add better touch area for mobile */
@media (max-width: 768px) {
  button,
  .nav-link,
  .project-link {
    min-height: 44px;
    min-width: 44px;
    padding: 0.8rem 1rem;
  }
}

/* Add responsive image handling */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Add lazy loading utility class */
.lazy-image {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.lazy-image.loaded {
  opacity: 1;
}

/* Add better image handling for projects */
.project-image {
  aspect-ratio: 16/9;
  object-fit: cover;
  border-radius: var(--border-radius-sm);
}

@media (max-width: 768px) {
  .project-image {
    aspect-ratio: 3/2;
  }
}

/* Add loading state styles */
.loading {
  position: relative;
  overflow: hidden;
}

.loading::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Add fade-in animation classes */
.fade-in-section {
  opacity: 0;
  transform: translateY(20px);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  will-change: opacity, visibility;
}

.fade-in-section.is-visible {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

/* Add these styles to ensure proper stacking */
.App {
  position: relative;
  z-index: 1;
}

/* Ensure the navigation stays on top */
.nav {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}
