/* projects.css */
.projects-section {
  padding: var(--spacing-xl) var(--spacing-md);
  background: transparent;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: rgba(17, 34, 64, 0.7);
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-md);
  transition: var(--transition);
  border: 1px solid rgba(100, 255, 218, 0.1);
  position: relative;
  overflow: hidden;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(2, 12, 27, 0.7);
  opacity: 0;
  transition: var(--transition);
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 30px -15px rgba(2, 12, 27, 0.7);
}

.project-card:hover::before {
  opacity: 1;
}

.project-title {
  color: var(--white);
  font-size: 1.5rem;
  margin-bottom: var(--spacing-sm);
  position: relative;
}

.project-description {
  color: var(--text-secondary);
  margin-bottom: var(--spacing-md);
  line-height: 1.6;
  position: relative;
}

.project-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: var(--spacing-md);
  position: relative;
}

.tech-tag {
  background: rgba(35, 53, 84, 0.7);
  color: var(--secondary);
  padding: 0.5rem 1rem;
  border-radius: var(--border-radius-sm);
  font-size: 0.9rem;
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: var(--transition);
}

.tech-tag:hover {
  background: rgba(100, 255, 218, 0.1);
  transform: translateY(-2px);
}

.project-links {
  display: flex;
  gap: var(--spacing-md);
  position: relative;
}

.project-link {
  color: var(--secondary);
  text-decoration: none;
  font-size: 1.1rem;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.project-link:hover {
  color: var(--white);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .projects-section {
    padding: var(--spacing-lg) var(--spacing-sm);
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
}

/* Add these styles for the project category buttons */
.category-filters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: var(--spacing-lg);
}

.filter-btn {
  background: transparent;
  color: var(--text-primary);
  border: 1px solid var(--secondary);
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.9rem;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
}

.filter-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: rgba(100, 255, 218, 0.1);
  transition: var(--transition);
}

.filter-btn:hover {
  color: var(--secondary);
  transform: translateY(-2px);
}

.filter-btn:hover::before {
  width: 100%;
}

.filter-btn.active {
  background: var(--secondary);
  color: var(--primary);
}

.filter-btn.active:hover {
  transform: translateY(-2px);
  background: var(--secondary);
  opacity: 0.9;
}
